	module.exports = {"data":{"markdownRemark":{"html":"<h2>Git Status</h2>\n<p>The <code>git status</code> commands displays the state of the working directory and the staging area. It displays paths that have differences between the <code>index</code> file and the current <code>HEAD</code> commit, paths that have differences between the working tree and the <code>index</code> file, and paths in the working tree that are not tracked by Git (and are not ignored by <a href=\"https://git-scm.com/docs/gitignore\">gitignore</a></p>\n<p><code>git status</code> command output does not show you any information regarding the committed project history. For this, you need to use <code>git log</code>.</p>\n<h3>Usage</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>git status</code></pre>\n      </div>\n<p>List which files are staged, unstaged, and untracked.</p>","fields":{"slug":"/git/git-status/"},"frontmatter":{"title":"Git Status"}}},"pathContext":{"slug":"/git/git-status/"}}

